import Vue from 'vue'
import Vuex from 'vuex'
import auth from "@/store/auth";
import main from "@/store/main";
import axios from "axios";
import router from "@/router";
import config from "@/config/config";

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isVisible: true,
    isVisibleCommonStat: false,
    shops: [],
    data: ''
  },
  mutations: {
    SHOP_NAMES(store, shops) {
      store.shops = shops
    },
    DATA_SHOP(store, data) {
      store.data = data
    }
  },
  actions: {
    LOGOUT() {
      axios.post(`${config.BASE_URL}/api/auth/logout`, {id: localStorage.getItem('seller-id')})
          .then(data => {
            localStorage.removeItem('token')
            router.push('/login?message=logout')
          })
          .catch(e => console.log(e))
    },
    async GET_DATA({commit}, data) {
      return await axios.post(
          `${config.URL_API_SELLER_ONLINE}`,
          data,
            {
              headers: {
                'Api-Key': localStorage.getItem('Zx21tL2eRrtOvvQq') || ''
              }
            }
          )
          .then(data => commit('DATA_SHOP', data.data))
          .catch(err => console.log(err))
    },
    TRACE({commit}, track) {
      return axios.post(`${config.BASE_URL}/api/track/trace`, track)
    }
  },
  getters: {
    MODAL(state) {
      return state.isVisible
    },
    SHOPS(state) {
      return state.shops
    },
    DATA(state) {
      return state.data
    }
  },
  modules: {
    auth,
    main
  }
})
